import React, { PureComponent } from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import styles from './DetailedIndividualPerson.module.scss';

import BasicInformation from './components/BasicInformation';
import AnalyticalRole from './components/AnalyticalRole';

import Loader from '../../Loader';

import { observer, inject } from 'mobx-react';
import { GraphStoreType } from '../../../stores/graph/graphStore';

import { CommonStoreType } from '../../../stores/common/commonStore';
import IndividualPersonStore from '../../../stores/modalData/individualPersonStore';
import { getDetailedNodeData } from '../../../utils/services/api/graph';
import Icon, { StatusesIcons } from '../../Icon';

type DetailedIndividualPersonProps = {
  [key: string]: any;
  graphStore?: GraphStoreType;
  commonStore?: CommonStoreType;
  individualPersonStore?: IndividualPersonStore;
};

type DetailedIndividualPersonState = {
  detailedData: any;
  analiticRolesData: any;
  documentsData: any;
  countriesData: any;
  invalidReasonData: any;
  pending: boolean;
};

/**
 * Компонент детализации узла
 */
@inject('graphStore', 'commonStore', 'individualPersonStore')
@observer
class DetailedIndividualPerson extends PureComponent<
  DetailedIndividualPersonProps,
  DetailedIndividualPersonState
> {
  constructor(props: DetailedIndividualPersonProps) {
    super(props);

    this.state = {
      detailedData: {},
      pending: true,
      analiticRolesData: [],
      documentsData: [],
      countriesData: [],
      invalidReasonData: [],
    };
  }

  /**
   * Метод запрашивает детальную информацию о узле
   */
  async componentDidMount() {
    const { orig_id, graphStore, commonStore, isShow, individualPersonStore } = this.props;
    const graph_id = graphStore?.previouslyFetchedGraphId || '';

    individualPersonStore?.resetData();
    individualPersonStore?.resetErrorInfo();
    individualPersonStore?.resetValidate();
    individualPersonStore?.resetAnalyticalValidate();
    individualPersonStore?.resetErrorAnalyticalInfo();

    if (isShow) {
      try {
        const detailedData: any = await getDetailedNodeData(graph_id, orig_id);
        this.setState({ detailedData: detailedData });

        if (detailedData.role_change_reason) {
          individualPersonStore?.setReasonID(detailedData.role_change_reason);

          const analyticRole = commonStore?.changeReasonsList.find(
            (role) => role.reason_id === detailedData.role_change_reason,
          );

          individualPersonStore?.setReasonName(analyticRole?.name);
        }

        individualPersonStore?.setAnaliticID(detailedData.role_id);
        individualPersonStore?.setSource(detailedData.role_info_source);
        individualPersonStore?.setComment(detailedData.analytical_role_description);
        individualPersonStore?.setAnaliticName(detailedData.analytical_role_name);

        individualPersonStore?.setAnaliticDocument(detailedData.attach_docs_role);
      } catch (err) {
        console.warn('Error on fetch node details: ', err.message);
      }
    }

    // Получение аналитических ролей
    const analiticRoles = commonStore?.analyticalRolesList;
    const analiticRolesFilter = analiticRoles
      ?.filter(
        (element: any) =>
          element.used_in_vsa &&
          element.control_areas.some((item: any) => item === graphStore?.controlArea),
      )
      .map(({ value }) => value);
    this.setState({ analiticRolesData: analiticRolesFilter });

    // Получение причин изменений роли
    const reasonInvalid = commonStore?.changeReasonsList;
    this.setState({ invalidReasonData: reasonInvalid?.map(({ name }) => name) });

    // Получение списка типов документов
    const documents = commonStore?.documentIdentitiesList;
    this.setState({ documentsData: documents?.map(({ name }) => name).sort() });

    // Получение списка стран
    const countries = commonStore?.countriesList;
    this.setState({ countriesData: countries?.map(({ name }) => name).sort() });
  }

  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const {
      onClose,
      taxpayer,
      Handmade,
      isShow,
      orig_id,
      disabledINN,
      graphStore,
      groupID,
      params,
      is_from_group,
      commonStore,
      isAnalytic,
      createLinkModeV3,
      modeLink,
      infoNodeModeV3,
    } = this.props;
    const { detailedData, documentsData, countriesData, analiticRolesData, invalidReasonData } =
      this.state;
    const disabled = false;

    const { node_change_statuses, change_histories } = detailedData;

    return (
      <div className={styles.wrapper}>
        <div className={styles.headerFixed}>
          <div className={styles.header} data-draggable>
            <div className={styles.headerLabel}>
              <span data-draggable>Детализация по физическому лицу</span>
              {graphStore!.pending ? (
                <StatusesIcons statuses={[node_change_statuses?.change_status] || []} />
              ) : null}
            </div>
            <Icon
              className={styles.close}
              name="close"
              onClick={onClose}
              theme={commonStore?.appTheme}
            />
          </div>
        </div>
        <div className={styles.separateLine} />
        {isShow && graphStore?.showAnalytic ? (
          <Tabs className={styles.tabs} defaultIndex={isAnalytic ? 2 : 0}>
            <TabList className={styles.tabList}>
              <Tab className={styles.tabItem} title={'Сведения о физическом лице'}>
                Основная информация
              </Tab>
              {isShow && graphStore?.showAnalytic && (
                <Tab
                  className={styles.tabItem}
                  title={'Сведения о присвоенной физическому лицу Аналитической роли в схеме'}
                >
                  <span>Аналитическая роль</span>
                </Tab>
              )}
              {/*{change_histories?.length > 0 && (*/}
              {/*    <Tab className={styles.tabItem}>*/}
              {/*      История <br /> изменений*/}
              {/*    </Tab>*/}
              {/*)}*/}
            </TabList>
            <TabPanel className={styles.tabPanel}>
              {Handmade || !isShow ? (
                <BasicInformation
                  disabled={disabled}
                  nodeData={taxpayer}
                  documents={documentsData}
                  countries={countriesData}
                  detailedData={detailedData.phys_person || {}}
                  handmade={Handmade}
                  orig_id={orig_id}
                  isShow={isShow}
                  isDisableINN={detailedData.is_vertex_has_edges}
                  disabledINN={disabledINN}
                  createLinkModeV3={createLinkModeV3}
                  modeLink={modeLink}
                  infoNodeModeV3={infoNodeModeV3}
                />
              ) : Object.keys(detailedData).length > 0 ? (
                <BasicInformation
                  disabled={disabled}
                  nodeData={taxpayer}
                  documents={documentsData}
                  countries={countriesData}
                  detailedData={detailedData.phys_person || {}}
                  orig_id={orig_id}
                  isShow={isShow}
                  isDisableINN={detailedData.is_vertex_has_edges}
                  handmade={false}
                  disabledINN={disabledINN}
                  createLinkModeV3={createLinkModeV3}
                  modeLink={modeLink}
                  infoNodeModeV3={infoNodeModeV3}
                />
              ) : (
                <div className={styles.loader}>
                  <Loader />
                </div>
              )}
            </TabPanel>
            <TabPanel className={styles.tabPanel}>
              <AnalyticalRole
                disabled={disabled}
                rolesData={analiticRolesData}
                invalidReasonData={invalidReasonData}
                detailedData={detailedData}
                orig_id={orig_id}
                groupID={groupID}
                params={params}
                is_from_group={is_from_group}
              />
            </TabPanel>
          </Tabs>
        ) : (
          <div className={styles.tabPanel}>
            {Handmade || !isShow ? (
              <BasicInformation
                disabled={disabled}
                nodeData={taxpayer}
                documents={documentsData}
                countries={countriesData}
                detailedData={detailedData.phys_person || {}}
                handmade={Handmade}
                orig_id={orig_id}
                isShow={isShow}
                isDisableINN={detailedData.is_vertex_has_edges}
                disabledINN={disabledINN}
                createLinkModeV3={createLinkModeV3}
                modeLink={modeLink}
                infoNodeModeV3={infoNodeModeV3}
              />
            ) : Object.keys(detailedData).length > 0 ? (
              <BasicInformation
                disabled={disabled}
                nodeData={taxpayer}
                documents={documentsData}
                countries={countriesData}
                detailedData={detailedData.phys_person || {}}
                orig_id={orig_id}
                isShow={isShow}
                isDisableINN={detailedData.is_vertex_has_edges}
                handmade={false}
                disabledINN={disabledINN}
                createLinkModeV3={createLinkModeV3}
                modeLink={modeLink}
                infoNodeModeV3={infoNodeModeV3}
              />
            ) : (
              <div className={styles.loader}>
                <Loader />
              </div>
            )}
          </div>
        )}
      </div>
    );
  }
}

export default DetailedIndividualPerson;
