import React, { DetailedHTMLProps, SVGAttributes } from 'react';

export const CheckboxIconDark = ({
  selected,
  someSelected,
}: { selected?: boolean; someSelected?: boolean } & DetailedHTMLProps<
  SVGAttributes<SVGSVGElement>,
  SVGSVGElement
>) => (
  <svg width="1em" height="1em" viewBox="0 0 14 14" fill="none" xmlns="http://www.w3.org/2000/svg">
    <rect
      x="0.5"
      y="0.5"
      width="13"
      height="13"
      fill={selected || someSelected ? 'white' : 'rgba(49, 49, 49, 1)'}
    />
    {selected && (
      <path
        d="M5.95047 8.66519L10.7763 3.83887L11.5191 4.58122L5.95047 10.1499L2.60938 6.80879L3.35173 6.06644L5.95047 8.66519Z"
        fill="rgba(49, 49, 49, 1)"
      />
    )}
    {someSelected && <rect x="3" y="6.5" width="8" height="2" fill="rgba(49, 49, 49, 1)" />}
    <rect x="0.5" y="0.5" width="13" height="13" stroke="white" />
  </svg>
);
