import React, { BaseSyntheticEvent } from 'react';
import { inputWithLabelPropTypes } from './InputWithLabel.props';
import styles from './InputWithLabel.module.scss';
import ReactTooltip from 'react-tooltip';

type InputWithLabelProps = {
  label: any;
  onChange: (event: BaseSyntheticEvent) => void;
  value?: string;
  className?: string;
  disabled?: boolean;
  type?: string;
  onKeyUp?: (event: BaseSyntheticEvent) => void;
  onCopy?: boolean;

  isToolTip?: boolean;
  toolTipText?: string;
  toolTipID?: string;
  withIcon?: boolean;
  maxLength?: any;
  placeholder?: string;
};

/**
 * Компонент поля ввода с надписью
 *
 * @param {string} label - надпись над полем ввода
 * @param {func} onChange - функция обрабатывающая вводимый текст
 * @param {string} value - значение поля ввода
 * @param {string} [className] - CSS класс стилей
 * @param {boolean} [disabled] - флаг показывающий блокировать ли элемент на изменение
 * @param {boolean} onCopy - возможность копирования
 * @param {boolean} isToolTip - наличие всплывающей подсказки
 * @param {string} toolTipText - текст всплывающей подсказки
 * @param {string} toolTipID - id всплывающей подсказки
 * @param {any} maxLength - максимальное число символов в input
 */
const InputWithLabel = (props: InputWithLabelProps) => {
  const {
    label,
    onChange,
    value,
    disabled,
    type,
    onKeyUp,
    onCopy,
    isToolTip,
    toolTipText,
    toolTipID,
    withIcon,
    maxLength,
    placeholder,
    ...rest
  } = props;

  return (
    <div
      data-tip={isToolTip ? 'React-tooltip' : ''}
      data-for={isToolTip ? toolTipID : null}
      {...rest}
    >
      <span className={styles.label}>{label}</span>
      <input
        disabled={disabled}
        className={`${styles.custom_input} ${
          onCopy ? styles.onCopy : disabled ? styles.disabled : ''
        } ${withIcon ? styles.withIcon : ''}
        `}
        type={type ? type : 'text'}
        value={value}
        onChange={onChange}
        onKeyUp={onKeyUp}
        maxLength={maxLength}
        placeholder={placeholder}
      />
      {isToolTip ? (
        <ReactTooltip place="bottom" type="dark" effect="float" id={toolTipID} delayShow={200}>
          <span className={styles.spanTooltip}>{toolTipText}</span>
        </ReactTooltip>
      ) : (
        ''
      )}
    </div>
  );
};

InputWithLabel.propTypes = inputWithLabelPropTypes;

InputWithLabel.defaultProps = {
  disabled: false,
};

export default InputWithLabel;
