import React, { PureComponent } from 'react';
import styles from './AnalyticalRole.module.scss';
import Dropdown from '../../../../Dropdown';

import InputWithLabel from '../../../../InputWithLabel';
import AttachmentFiles from '../AttachmentFiles';
import ActionButton from '../../../../ActionButton';

import IndividualPersonStore from '../../../../../stores/modalData/individualPersonStore';
import { inject, observer } from 'mobx-react';
import { action } from 'mobx';
import { CommonStoreType } from '../../../../../stores/common/commonStore';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { ModalStoreType } from '../../../../../stores/modalStore';
import { MODAL_DETAILED_INDIVIDUAL_PERSON } from '../../../constants/modalNames';

import { ReactComponent as ErrorIcon } from './assets/error.svg';
import Error from '../../../Error';

interface AnalyticalRoleProps {
  disabled?: boolean;
  detailedData: any;
  rolesData?: any;
  invalidReasonData?: any;
  individualPersonStore?: IndividualPersonStore;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
  modalStore?: ModalStoreType;
  orig_id?: any;
  groupID?: any;
  params?: any;
  is_from_group?: boolean;
}

interface AnalyticalRoleState {
  detailedData: any;
}

/**
 * Компонент Аналитическая роль физ.лица
 */
@inject('individualPersonStore', 'commonStore', 'graphStore', 'modalStore')
@observer
class AnalyticalRole extends PureComponent<AnalyticalRoleProps, AnalyticalRoleState> {
  constructor(props: AnalyticalRoleProps) {
    super(props);

    const { detailedData } = props;

    this.state = {
      detailedData,
    };
  }

  @action componentDidMount() {
    const { individualPersonStore, commonStore } = this.props;

    const { detailedData } = this.state;

    individualPersonStore?.setIsValidFile(false);

    if (detailedData.role_change_reason) {
      individualPersonStore?.setReasonID(detailedData.role_change_reason);

      const analyticRole = commonStore?.changeReasonsList.find(
        (role) => role.reason_id === detailedData.role_change_reason,
      );

      individualPersonStore?.setReasonName(analyticRole?.name);
    }

    individualPersonStore?.setAnaliticID(detailedData.role_id);
    individualPersonStore?.setSource(detailedData.role_info_source);
    individualPersonStore?.setComment(detailedData.analytical_role_description);
    individualPersonStore?.setAnaliticName(detailedData.analytical_role_name);

    individualPersonStore?.setAnaliticDocument(detailedData.attach_docs_role);
  }

  /**
   * Метод изменения наименования аналитической роли
   */
  @action _onNameChange = (data: string): void => {
    const { individualPersonStore, commonStore } = this.props;

    individualPersonStore?.setAnaliticName(data);

    const roleID = commonStore?.analyticalRolesList.find((role) => role.value === data);
    individualPersonStore?.setAnaliticID(roleID?.role_id);
  };

  /**
   * Метод изменения причины изменения
   */
  @action _onReasonChange = (data: string): void => {
    const { individualPersonStore, commonStore } = this.props;

    individualPersonStore?.setReasonName(data);

    const analiticReason = commonStore?.changeReasonsList.find((reason) => reason.name === data);
    individualPersonStore?.setReasonID(analiticReason?.reason_id);
  };

  // Валидация полей
  _validate = () => {
    const { individualPersonStore } = this.props;

    individualPersonStore?.resetAnalyticalValidate();
    individualPersonStore?.resetErrorAnalyticalInfo();

    // Наличие Наименования
    if (!individualPersonStore?.analiticData.role_id) {
      individualPersonStore?.errorsAnalytical.push(
        'Не выбрана аналитическая роль НП: Выберите значение аналитической роли',
      );
      individualPersonStore?.setFieldsAnalyticalError('role_id', true);
    }

    // Наличие Источника
    if (
      !individualPersonStore?.analiticData.role_info_source ||
      !individualPersonStore?.analiticData.role_info_source.trim()
    ) {
      individualPersonStore?.errorsAnalytical.push(
        'Не заполнен Источник информации для изменения аналитической роли. Опишите источник информации',
      );
      individualPersonStore?.setFieldsAnalyticalError('role_info_source', true);
    }

    // Количество символов для Источника
    if (
      individualPersonStore?.analiticData.role_info_source &&
      individualPersonStore?.analiticData.role_info_source.length > 4000
    ) {
      individualPersonStore?.errorsAnalytical.push(
        'Количество допустимых символов в поле Источник информации превышает 4000',
      );
      individualPersonStore?.setFieldsAnalyticalError('role_info_source', true);
    }

    // Наличие Причины
    if (!individualPersonStore?.analiticData.role_change_reason) {
      individualPersonStore?.errorsAnalytical.push(
        'Не заполнена Причина изменения аналитической роли. Опишите причину изменения аналитической роли',
      );
      individualPersonStore?.setFieldsAnalyticalError('role_change_reason', true);
    }

    if (individualPersonStore?.errorsAnalytical.length === 0) {
      individualPersonStore?.setIsValidAnalytical(false);
    } else {
      individualPersonStore?.setIsValidAnalytical(true);
    }
  };

  /**
   * Изменение информации об аналитической роли ФЛ
   */
  async _changeInformation() {
    const {
      graphStore,
      individualPersonStore,
      orig_id,
      modalStore,
      groupID,
      params,
      is_from_group,
    } = this.props;

    individualPersonStore?.setDocuments(individualPersonStore?.analiticDocument);

    this._validate();

    if (!individualPersonStore?.isValidAnalytical) {
      try {
        await graphStore!.changeRoles(individualPersonStore?.analiticData, orig_id);
        if (is_from_group) {
          graphStore!.fetchDetailedGroup(groupID, params);
        }
      } catch (err) {
        console.warn('Error on update information: ', err.message);
      }

      modalStore?.closeModal(MODAL_DETAILED_INDIVIDUAL_PERSON);
    }
  }

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const {
      props: {
        disabled,
        individualPersonStore,
        rolesData,
        invalidReasonData,
        modalStore,
        graphStore,
        commonStore,
      },
    } = this;

    return (
      <div className={styles.wrapper}>
        <div className={styles.dataHeader}>
          <span className={styles.headerLabel}>Аналитическая роль налогоплательщика</span>
        </div>
        <div className={styles.container}>
          <Dropdown
            disabled={disabled}
            dropdownStyle={styles.dropdownBig}
            labelStyle={styles.dropdownLabel}
            containerStyle={
              individualPersonStore?.fieldsErrorAnalytic.role_id
                ? `${styles.dropdownContainer} ${styles.dropdownContainer__error}`
                : `${styles.dropdownContainer} `
            }
            valueStyle={styles.dropdownValue}
            label={'Наименование аналитической роли'}
            value={individualPersonStore?.analiticName}
            items={rolesData}
            onSelect={this._onNameChange}
            theme={commonStore?.appTheme}
            isToolTip={true}
            toolTipText="Выбор аналитической роли из перечня"
            toolTipID="roleToolTipAnaliticFL"
          />
        </div>

        <div className={styles.container}>
          <InputWithLabel
            disabled={disabled}
            className={
              individualPersonStore?.fieldsErrorAnalytic.role_info_source
                ? `${styles.containerMedium} ${styles.containerMedium__error}`
                : `${styles.containerMedium} `
            }
            value={individualPersonStore?.analiticData.role_info_source}
            onChange={(event) => {
              individualPersonStore?.setSource(event.target.value);
            }}
            label="Источник информации"
            isToolTip={true}
            toolTipText="Описание источника информации для изменения аналитической роли"
            toolTipID="sourceToolTipAnaliticFL"
          />

          <div>
            <Dropdown
              disabled={disabled}
              dropdownStyle={styles.dropdownMedium}
              labelStyle={styles.dropdownLabel}
              containerStyle={
                individualPersonStore?.fieldsErrorAnalytic.role_change_reason
                  ? `${styles.dropdownContainer} ${styles.dropdownContainer__error}`
                  : `${styles.dropdownContainer} `
              }
              valueStyle={styles.dropdownValue}
              label="Причина изменения"
              value={individualPersonStore?.analiticData.changeName}
              items={invalidReasonData}
              onSelect={this._onReasonChange}
              theme={commonStore?.appTheme}
              isToolTip={true}
              toolTipText="Описание причины изменения аналитической роли"
              toolTipID="reasonToolTipAnaliticFL"
            />
          </div>
        </div>

        <div className={styles.container}>
          <div className={`${styles.containerLarge} ${styles.commentsContainer}`}>
            <span className={styles.dataLabel}>Комментарий</span>
            <textarea
              disabled={disabled}
              className={styles.commentsTextarea}
              value={individualPersonStore?.analiticData.role_description}
              onChange={(event) => {
                individualPersonStore?.setComment(event.target.value);
              }}
            />
          </div>
        </div>

        {individualPersonStore?.isValidFile && (
          <div className={`${styles.container_validation}`}>
            <ErrorIcon className={`${styles.error_icon}`} />
            Указанный файл {individualPersonStore?.fileName} имеет неподдерживаемый формат или
            испорчен. Укажите корректный файл в одном из форматов: .rtf, .doc, .docx, .pdf, .xls,
            .xlsx, .txt.
          </div>
        )}

        <AttachmentFiles individualPersonStore={individualPersonStore} />

        {individualPersonStore?.errorsAnalytical.map((item: any) => (
          <Error isValidFile={individualPersonStore?.isValidAnalytical} text={item} />
        ))}
        {!graphStore?.visibleNodes && graphStore?.ableEdit && (
          <div className={styles.actions}>
            <div className={styles.action}>
              <ActionButton
                label="Сохранить"
                onClick={() => this._changeInformation()}
                disabled={disabled}
                title="Сохранение изменений"
              />
            </div>
            <div className={styles.action}>
              <ActionButton
                label="Отменить"
                onClick={() => {
                  modalStore?.closeModal(MODAL_DETAILED_INDIVIDUAL_PERSON);
                }}
                isSecondary={true}
                disabled={disabled}
                title="Отмена изменений"
              />
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default AnalyticalRole;
