import React, { PureComponent } from 'react';
import styles from './Error.module.scss';

import { observer } from 'mobx-react';

import { ReactComponent as ErrorIcon } from './assets/error.svg';

type ErrorProps = {
  [key: string]: any;
  text?: any;
  isValidFile?: boolean;
};

/**
 * Отображение ошибок, связанных с валидацией
 */
@observer
class Error extends PureComponent<ErrorProps> {
  /**
   * Метод отрисовывает элементы на странице
   */
  render() {
    const { isValidFile, text } = this.props;

    if (!isValidFile) return null;

    return (
      <div className={`${styles.container_validation}`}>
        <ErrorIcon className={`${styles.error_icon}`} />
        {text}
      </div>
    );
  }
}

export default Error;
