import React, { PureComponent, RefObject } from 'react';
import { inject, observer } from 'mobx-react';

import ActionButton from 'components/ActionButton';
import IndividualPersonStore from '../../../../../stores/modalData/individualPersonStore';

import styles from './AttachmentFiles.module.scss';
import Icon from '../../../../Icon';
import { CommonStoreType } from '../../../../../stores/common/commonStore';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';
import { ReactComponent as FileIcon } from 'components/FileUploader/assets/file.svg';

type AttachmentFilesProps = {
  individualPersonStore?: IndividualPersonStore;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
  data?: any;
};

const acceptingTableTypes =
  '.xls,.xlsx,application/vnd.openxmlformats-officedocument.spreadsheetml.sheet,application/vnd.ms-excel';
const acceptingDocTypes =
  '.doc,.docx,application/vnd.openxmlformats-officedocument.wordprocessingml.document,application/msword';
const commonAccept = acceptingTableTypes + acceptingDocTypes + '.pdf,application/pdf,.txt,.rft';

@inject('individualPersonStore', 'commonStore', 'graphStore')
@observer
class AttachmentFiles extends PureComponent<AttachmentFilesProps> {
  fileInput: RefObject<HTMLInputElement>;

  constructor(props: AttachmentFilesProps) {
    super(props);
    this.fileInput = React.createRef();
  }

  /**
   * Метод удаляет прикрепленный файл
   */
  _onItemRemove = (e: MouseEvent, name: string): void => {
    e.stopPropagation();

    const { individualPersonStore } = this.props;
    const files = individualPersonStore?.analiticDocument;

    const filteredFiles = files?.filter((item: any) => item.file_name !== name);

    individualPersonStore?.setAnaliticDocument(filteredFiles);
  };

  /**
   * Метод отрисовывает список файлов
   */
  _renderFileList = () => {
    const { individualPersonStore, commonStore, graphStore } = this.props;
    const files = individualPersonStore?.analiticDocument;

    return files?.map((file: any) => (
      <div
        key={'file-list-item-' + file.file_name}
        className={styles.fileItem}
        onClick={() => graphStore?.getUploadedDocument(file.document_id, file.file_name)}
      >
        <div className={styles.fileLabel}>
          <FileIcon className={styles.fileIcon} />
          <span className={styles.fileItemLabel}>{file.file_name}</span>
        </div>
        <Icon
          className={styles.fileItemRemove}
          name="close"
          onClick={(e: MouseEvent) => this._onItemRemove(e, file.file_name)}
          theme={commonStore?.appTheme}
        />
      </div>
    ));
  };

  render() {
    const { individualPersonStore } = this.props;

    return (
      <>
        <div>
          <input
            type="file"
            accept={commonAccept}
            ref={this.fileInput}
            onChange={(e) => {
              //@ts-ignore
              const files = [...e.target.files];
              e.target.value = '';

              const streams = files.map((file: File) => {
                return {
                  //@ts-ignore
                  stream: file,
                  file_name: file.name,
                };
              });

              let types = ['.rtf', '.doc', '.docx', '.pdf', '.xls', '.xlsx', '.txt'];

              if (types.some((type) => streams[0]?.file_name.includes(type))) {
                individualPersonStore?.setAnaliticDocument([
                  ...individualPersonStore?.analiticDocument,
                  ...streams,
                ]);
                individualPersonStore?.setIsValidFile(false);
              } else {
                individualPersonStore?.setIsValidFile(true);
                individualPersonStore?.setFileName(streams[0]?.file_name);
              }
            }}
            multiple
            style={{ display: 'none' }}
          />
          <div className={styles.fileContainer}>
            <span className={styles.data_label}>Добавить файлы</span>
            <div className={styles.actions}>
              <div className={styles.action}>
                <ActionButton
                  label="Выберите файлы"
                  onClick={() => this.fileInput.current?.click()}
                  title="Приложение документов-оснований для присвоения Аналитических ролей"
                  isSecondary
                />
              </div>
              <div className={styles.filesTooltip}>
                <span className={styles.title}>Перетащие сюда файлы для загрузки</span>
              </div>
            </div>

            <div className={styles.fileList}>
              {/*
                    // @ts-ignore*/}
              {individualPersonStore?.analiticDocument?.length > 0 && this._renderFileList()}
            </div>
          </div>
        </div>
      </>
    );
  }
}

export default AttachmentFiles;
