import React, { PureComponent } from 'react';
import { Checkbox } from '@fdc/vsa-react-lib';
import styles from './BasicInformation.module.scss';
import InputWithLabel from '../../../../InputWithLabel';
import Radio from 'components/Radio';

import { DatePickerInput } from 'rc-datepicker';
import 'rc-datepicker/lib/style.css';
import 'moment/locale/ru.js';

import ReactTooltip from 'react-tooltip';

import Dropdown from '../../../../Dropdown';
import ActionButton from 'components/ActionButton';

import IndividualPersonStore from '../../../../../stores/modalData/individualPersonStore';
import { inject, observer } from 'mobx-react';
import { action } from 'mobx';
import { CommonStoreType } from '../../../../../stores/common/commonStore';
import { GraphStoreType } from '../../../../../stores/graph/graphStore';

import { MODAL_CREATE_LINK, MODAL_DETAILED_INDIVIDUAL_PERSON } from '../../../constants/modalNames';
import { ModalStoreType } from '../../../../../stores/modalStore';

import { ReactComponent as QuestionIcon } from '../../assets/question.svg';
import { ReactComponent as CalendarIcon } from '../../assets/calendarIcon.svg';

import Error from '../../../Error';
import { CheckboxIcon } from '../../../DetailedGroup/components/CheckboxIcon';

import { CheckboxIconDark } from '../../../DetailedGroup/components/CheckboxIconDark';

interface BasicInformationProps {
  nodeData: any;
  detailedData: any;
  disabled?: boolean;
  handmade?: boolean;
  documents: any;
  countries: any;
  orig_id: any;
  isShow: boolean;
  individualPersonStore?: IndividualPersonStore;
  modalStore?: ModalStoreType;
  commonStore?: CommonStoreType;
  graphStore?: GraphStoreType;
  isDisableINN: boolean;
  disabledINN: boolean;
  createLinkModeV3?: boolean;
  modeLink?: string;
  infoNodeModeV3?: any;
}

interface BasicInformationState {
  detailedData: any;
  isFillRequired: boolean;
}

/**
 * Компонент Основные сведения о ФЛ
 */
@inject('individualPersonStore', 'commonStore', 'graphStore', 'modalStore')
@observer
class BasicInformation extends PureComponent<BasicInformationProps, BasicInformationState> {
  constructor(props: BasicInformationProps) {
    super(props);

    const { detailedData } = props;

    this.state = {
      detailedData,
      isFillRequired: false,
    };
  }

  @action componentDidMount() {
    const { individualPersonStore, handmade, commonStore, nodeData } = this.props;

    const { detailedData } = this.state;

    if (!handmade) {
      individualPersonStore?.setData(detailedData);

      if (nodeData?.node.inn) {
        individualPersonStore?.setINN(nodeData.node.inn);
      }

      if (nodeData?.node.short_name) {
        individualPersonStore?.setName(nodeData.node.short_name);
      }

      if (nodeData?.node.date_of_birth) {
        individualPersonStore?.setDate(nodeData.node.date_of_birth);
      }

      if (detailedData?.citizenship_id) {
        // Установление гражданства
        const citizenship = commonStore?.countriesList.find(
          (city) => city.country_id === detailedData.citizenship_id,
        );
        individualPersonStore?.setCitizenshipName(citizenship?.name);
        individualPersonStore?.setCitizenshipID(citizenship?.country_id);
      }

      if (detailedData?.document_identity) {
        // Установление типа документа
        const document = commonStore?.documentIdentitiesList.find(
          (document) => document.document_identity_id === detailedData.document_identity,
        );
        individualPersonStore?.setDocumentName(document?.name);
        individualPersonStore?.setDocumentID(document?.document_identity_id);
      }

      if (!detailedData?.person_attribute) {
        this._onPersonAttribute('CountryCitizen');
      }
    } else {
      this._onPersonAttribute('CountryCitizen');
    }
  }

  /**
   * Метод изменения гражданства
   */
  @action _onCitizenshipNameChange = (data: string): void => {
    const { individualPersonStore, commonStore } = this.props;

    if (individualPersonStore?.data?.person_attribute === 'ForeignPhysPerson') {
      individualPersonStore?.setCitizenshipName(data);

      const citizenshipID = commonStore?.countriesList.find((city) => city.name === data);
      individualPersonStore?.setCitizenshipID(citizenshipID?.country_id);
    }

    this.checkFillRequired();
  };

  /**
   * Метод изменения типа документа
   */
  @action _onDocumentType = (data: string): void => {
    const { individualPersonStore, commonStore } = this.props;

    individualPersonStore?.setDocumentName(data);

    const documentID = commonStore?.documentIdentitiesList.find(
      (document) => document.name === data,
    );
    individualPersonStore?.setDocumentID(documentID?.document_identity_id);
  };

  /**
   * Метод признака персоны
   */
  @action _onPersonAttribute = (data: string): void => {
    const { individualPersonStore, commonStore } = this.props;

    individualPersonStore?.setPersonAttribute(data);

    if (data === 'CountryCitizen') {
      individualPersonStore?.setCitizenshipName('Россия');

      const citizenshipID = commonStore?.countriesList.find((city) => city.name === 'Россия');
      individualPersonStore?.setCitizenshipID(citizenshipID?.country_id);
    } else {
      individualPersonStore?.setCitizenshipName(null);

      individualPersonStore?.setCitizenshipID(null);
    }
  };

  // Валидация полей
  _validate = () => {
    const { individualPersonStore } = this.props;

    individualPersonStore?.resetValidate();
    individualPersonStore?.resetErrorInfo();

    // Если есть ИНН, то количество цифр должно быть 12
    if (
      individualPersonStore?.data.inn &&
      individualPersonStore?.data.inn.length !== 12 &&
      individualPersonStore?.data?.person_attribute === 'CountryCitizen'
    ) {
      individualPersonStore?.errors.push('ИНН физ. лица должен содержать 12 цифр');
      individualPersonStore?.setFieldsError('inn', true);
    }

    // Наличие ФИО
    if (!individualPersonStore?.data.name || !individualPersonStore?.data.name.trim()) {
      individualPersonStore?.errors.push('Не заполнено ФИО');
      individualPersonStore?.setFieldsError('name', true);
    }

    // Количество символов для ФИО
    if (individualPersonStore?.data.name && individualPersonStore?.data.name.length > 4000) {
      individualPersonStore?.errors.push(
        'Количество допустимых символов в поле ФИО превышает 4000',
      );
      individualPersonStore?.setFieldsError('name', true);
    }

    if (individualPersonStore?.data.date_of_birth === 'Invalid date') {
      individualPersonStore?.errors.push('Дата рождения заполнена неверно');
      individualPersonStore?.setFieldsError('date_of_birth', true);
    }

    if (
      individualPersonStore?.data.date_of_birth &&
      individualPersonStore?.data.date_of_birth !== 'Invalid date'
    ) {
      let nowDate = new Date();
      let dateBirth = new Date(individualPersonStore?.data.date_of_birth);
      if (nowDate < dateBirth) {
        individualPersonStore?.errors.push('Дата рождения не может быть больше текущей даты');
        individualPersonStore?.setFieldsError('date_of_birth', true);
      }
    }

    // Наличие Признак гражданства
    if (!individualPersonStore?.data.person_attribute) {
      individualPersonStore?.errors.push('Не выбран признак гражданства');
      individualPersonStore?.setFieldsError('person_attribute', true);
    }

    // Наличие Гражданство
    if (
      !individualPersonStore?.citizenship_name &&
      individualPersonStore?.data.person_attribute === 'ForeignPhysPerson'
    ) {
      individualPersonStore?.errors.push('Не выбрано гражданство');
      individualPersonStore?.setFieldsError('citizenship_name', true);
    }

    if (individualPersonStore?.errors.length === 0) {
      individualPersonStore?.setIsValidBasic(false);
    } else {
      individualPersonStore?.setIsValidBasic(true);
    }
  };

  /**
   * Метод изменения физического лица
   */
  _changeNode = async () => {
    const { graphStore, individualPersonStore, orig_id, modalStore } = this.props;

    if (!individualPersonStore?.data.person_attribute) {
      this._onPersonAttribute('CountryCitizen');
    }

    this._validate();

    if (!individualPersonStore?.isValidBasic) {
      try {
        await graphStore!.changeRoles(
          {
            phys_person: individualPersonStore?.data,
            attach_docs_role: individualPersonStore?.analiticData.attach_docs_role,
            role_change_reason: individualPersonStore?.analiticData.role_change_reason,
            role_description: individualPersonStore?.analiticData.role_description,
            role_id: individualPersonStore?.analiticData.role_id,
            role_info_source: individualPersonStore?.analiticData.role_info_source,
          },
          orig_id,
        );
      } catch (err) {
        console.warn('Error on update information: ', err.message);
      }

      modalStore?.closeModal(MODAL_DETAILED_INDIVIDUAL_PERSON);
    }
  };

  /**
   * Создание физического лица на графе
   */
  _createNewFL = async () => {
    const {
      graphStore,
      individualPersonStore,
      nodeData,
      modalStore,
      disabledINN,
      createLinkModeV3,
      modeLink,
      infoNodeModeV3,
    } = this.props;

    if (nodeData?.inn) {
      individualPersonStore?.setINN(nodeData.inn);
    }
    if (nodeData?.name) {
      individualPersonStore?.setName(nodeData.name);
    }

    if (nodeData?.date_of_birth) {
      let convertDate = new Date(nodeData.date_of_birth);
      convertDate = new Date(
        convertDate.getUTCFullYear(),
        convertDate.getUTCMonth(),
        convertDate.getUTCDate(),
      );
      individualPersonStore?.setDate(convertDate);
    }

    if (disabledINN) {
      individualPersonStore?.setManualInput(false);
    } else {
      individualPersonStore?.setManualInput(true);
    }

    if (!individualPersonStore?.data.person_attribute) {
      this._onPersonAttribute('CountryCitizen');
    }

    this._validate();

    if (!individualPersonStore?.isValidBasic) {
      if (createLinkModeV3) {
        let fromNode, toNode;
        if (modeLink === 'from') {
          fromNode = individualPersonStore?.data;
          toNode = infoNodeModeV3;
        } else {
          fromNode = infoNodeModeV3;
          toNode = individualPersonStore?.data;
        }
        const isFL = true;
        modalStore?.openModal(MODAL_CREATE_LINK, {
          fromNode,
          toNode,
          createLinkModeV3,
          modeLink,
          isFL,
        });
      } else {
        try {
          await graphStore!.createNodeFL({
            phys_person: individualPersonStore?.data,
          });
        } catch (err) {
          console.warn('Error on create node: ', err.message);
        }
      }
      modalStore?.closeModal(MODAL_DETAILED_INDIVIDUAL_PERSON);
    }
  };

  checkFillRequired() {
    const { individualPersonStore } = this.props;

    if (individualPersonStore?.data.person_attribute === 'ForeignPhysPerson') {
      if (
        individualPersonStore?.data.name &&
        individualPersonStore?.data.name.trim() &&
        individualPersonStore?.citizenship_name
      ) {
        this.setState({ isFillRequired: true });
      } else {
        this.setState({ isFillRequired: false });
      }
    } else {
      if (individualPersonStore?.data.name && individualPersonStore?.data.name.trim()) {
        this.setState({ isFillRequired: true });
      } else {
        this.setState({ isFillRequired: false });
      }
    }
  }

  /**
   * Метод отрисовывает элементы
   */
  render() {
    const {
      props: {
        disabled,
        nodeData,
        individualPersonStore,
        documents,
        countries,
        isShow,
        modalStore,
        isDisableINN,
        handmade,
        disabledINN,
        commonStore,
        graphStore,
      },
      state: { isFillRequired },
    } = this;

    let convertDate = new Date();
    if (nodeData?.date_of_birth) {
      convertDate = new Date(nodeData.date_of_birth);
      convertDate = new Date(
        convertDate.getUTCFullYear(),
        convertDate.getUTCMonth(),
        convertDate.getUTCDate(),
      );
    } else if (individualPersonStore?.data?.date_of_birth) {
      convertDate = new Date(individualPersonStore?.data?.date_of_birth);
      convertDate = new Date(
        convertDate.getUTCFullYear(),
        convertDate.getUTCMonth(),
        convertDate.getUTCDate(),
      );
    }

    return (
      <div className={styles.wrapper}>
        <div className={styles.dataHeader}>
          <span className={styles.headerLabel}>Основные сведения о ФЛ</span>
        </div>
        <div className={styles.container}>
          <div className={styles.row}>
            <InputWithLabel
              placeholder={'Укажите ИНН'}
              disabled={
                disabledINN
                  ? disabledINN
                  : handmade
                  ? false
                  : individualPersonStore?.data?.is_manual_input
                  ? isDisableINN
                  : true
              }
              className={
                individualPersonStore?.fieldsError.inn
                  ? `${styles.containerMinor} ${styles.containerMinor__error}`
                  : `${styles.containerMinor} `
              }
              value={
                isShow
                  ? individualPersonStore?.data?.inn
                  : individualPersonStore?.data?.inn
                  ? individualPersonStore?.data?.inn
                  : nodeData?.inn
                  ? nodeData.inn
                  : nodeData?.node?.inn
                  ? nodeData?.node?.inn
                  : ''
              }
              onChange={(event) => {
                individualPersonStore?.setINN(event.target.value);
              }}
              onKeyUp={(e) => {
                e.target.value = e.target.value.replace(/[^\d]/g, '');
              }}
              onCopy={true}
              isToolTip={true}
              toolTipText={
                !individualPersonStore?.data?.person_attribute ||
                individualPersonStore?.data?.person_attribute === 'CountryCitizen'
                  ? 'Ввод ИНН физ. лица (должен содержать 12 цифр)'
                  : 'Ввод ИНН физ. лица'
              }
              toolTipID="innToolTipFL"
              maxLength={
                (!individualPersonStore?.data?.person_attribute ||
                  individualPersonStore?.data?.person_attribute === 'CountryCitizen') &&
                12
              }
              label={
                <div className={styles.labelWithIcon}>
                  <span>ИНН</span>
                  <QuestionIcon
                    data-tip="React-tooltip"
                    data-for="innIconToolTip"
                    className={styles.question_icon}
                  />
                  <ReactTooltip
                    place="right"
                    type="light"
                    effect="float"
                    backgroundColor={'#EDEDED'}
                    textColor={'#565656'}
                    id="innIconToolTip"
                  >
                    <span className={styles.spanTooltip}>При наличии</span>
                  </ReactTooltip>
                </div>
              }
            />

            <InputWithLabel
              placeholder={'Укажите ФИО'}
              disabled={
                disabledINN
                  ? disabledINN
                  : handmade
                  ? false
                  : individualPersonStore?.data?.is_manual_input
                  ? isDisableINN
                  : true
              }
              className={
                individualPersonStore?.fieldsError.name
                  ? `${styles.containerLarge} ${styles.containerLarge__error}`
                  : `${styles.containerLarge} `
              }
              value={
                individualPersonStore?.data?.name
                  ? individualPersonStore?.data?.name
                  : nodeData?.name
                  ? nodeData.name
                  : nodeData?.node?.short_name
                  ? nodeData?.node?.short_name
                  : ''
              }
              onChange={(event) => {
                individualPersonStore?.setName(event.target.value);
                this.checkFillRequired();
              }}
              label="ФИО (обязательно)"
              onCopy
              isToolTip
              toolTipText="Укажите Фамилию Имя Отчество"
              toolTipID="fioToolTipFL"
            />

            <div className={styles.containerData}>
              <span className={styles.dataSpan}>Дата рождения</span>

              <CalendarIcon className={styles.calendar_icon} />

              <DatePickerInput
                locale="ru"
                placeholder={'01.01.1970'}
                displayFormat="DD.MM.YYYY"
                showOnInputClick
                iconClassName=""
                className={
                  individualPersonStore?.fieldsError.date_of_birth
                    ? `${styles.dataInputError}`
                    : `${styles.dataInput}`
                }
                value={
                  individualPersonStore?.data?.date_of_birth
                    ? convertDate
                    : nodeData?.date_of_birth
                    ? convertDate
                    : nodeData?.node?.date_of_birth
                    ? nodeData?.node?.date_of_birth
                    : ''
                }
                onChange={(date: any) => {
                  individualPersonStore?.setDate(date);
                }}
                data-tip="React-tooltip"
                data-for="dateBirthToolTip"
                disabled={
                  disabledINN
                    ? disabledINN
                    : handmade
                    ? false
                    : individualPersonStore?.data?.is_manual_input
                    ? isDisableINN
                    : true
                }
              />

              <ReactTooltip
                place="bottom"
                type="dark"
                effect="float"
                id="dateBirthToolTip"
                delayShow={200}
              >
                <span className={styles.spanTooltip}>Укажите дату рождения</span>
              </ReactTooltip>
            </div>
          </div>
        </div>
        <div className={styles.separateLine} />
        <div className={styles.dataHeader}>
          <span className={styles.dataHeaderLabel}>Гражданство, удостоверение личности, адрес</span>
        </div>
        <div
          className={styles.container}
          data-tip="React-tooltip"
          data-for="personAttributeToolTip"
        >
          <Radio
            className={styles.radio}
            items={[
              { value: 'CountryCitizen', label: 'Гражданин РФ' },
              { value: 'ForeignPhysPerson', label: 'Иностранное ФЛ' },
              { value: 'StatelessPerson', label: 'Лицо без гражданства' },
            ]}
            value={
              individualPersonStore?.data?.person_attribute
                ? individualPersonStore?.data?.person_attribute
                : 'CountryCitizen'
            }
            onChange={this._onPersonAttribute}
            iconFirst
          />
          <ReactTooltip
            place="bottom"
            type="dark"
            effect="float"
            id="personAttributeToolTip"
            delayShow={200}
          >
            <span className={styles.spanTooltip}>Выбор признака гражданства</span>
          </ReactTooltip>
        </div>
        <div className={styles.container}>
          <div>
            <Dropdown
              disabled={
                individualPersonStore?.data?.person_attribute === 'CountryCitizen' ||
                individualPersonStore?.data?.person_attribute === 'StatelessPerson' ||
                !graphStore?.showCountries
              }
              dropdownStyle={styles.dropdown}
              labelStyle={styles.dropdownLabel}
              listWrapper={styles.listWrapper}
              containerStyle={
                individualPersonStore?.fieldsError.citizenship_name
                  ? `${styles.dropdownContainer} ${styles.dropdownContainer__error}`
                  : `${styles.dropdownContainer} `
              }
              valueStyle={`${styles.dropdownValue} ${disabled ? styles.dropdownValueDisabled : ''}`}
              value={
                individualPersonStore?.citizenship_name
                  ? individualPersonStore?.citizenship_name
                  : individualPersonStore?.data?.person_attribute === 'CountryCitizen'
                  ? 'Россия'
                  : ''
              }
              items={countries}
              onSelect={this._onCitizenshipNameChange}
              label={
                <div className={styles.labelDropdownWithIcon}>
                  <span>Гражданство</span>
                  <QuestionIcon
                    data-tip="React-tooltip"
                    data-for="citizenshipIconToolTip"
                    className={styles.question_icon}
                  />
                  <ReactTooltip
                    place="right"
                    type="light"
                    effect="float"
                    backgroundColor={'#EDEDED'}
                    textColor={'#565656'}
                    id="citizenshipIconToolTip"
                  >
                    <span className={styles.spanTooltip}>При наличии</span>
                  </ReactTooltip>
                </div>
              }
              theme={commonStore?.appTheme}
              isToolTip={true}
              toolTipText="Выбор страны гражданства"
              toolTipID="citizenshipToolTipFL"
              placeholder={
                individualPersonStore?.data?.person_attribute !== 'StatelessPerson'
                  ? 'Выберите гражданство из списка'
                  : ''
              }
            />
          </div>
          <InputWithLabel
            placeholder={'Укажите город / область / населенный пункт и тд'}
            disabled={disabled}
            className={styles.containerMedium}
            value={individualPersonStore?.data?.place_of_birth}
            onChange={(event) => {
              individualPersonStore?.setPlaceOfBirth(event.target.value);
            }}
            label="Место рождения"
          />
        </div>
        <div className={styles.container}>
          <div>
            <Dropdown
              disabled={disabled || !graphStore?.showDocuments}
              dropdownStyle={styles.dropdown}
              labelStyle={styles.dropdownLabel}
              containerStyle={styles.dropdownContainer}
              valueStyle={`${styles.dropdownValue} ${disabled ? styles.dropdownValueDisabled : ''}`}
              value={individualPersonStore?.document_name}
              items={documents}
              onSelect={this._onDocumentType}
              label="Документ, удостоверяющий личность"
              theme={commonStore?.appTheme}
              listWrapper={styles.listWrapper}
              placeholder={'Выберите документ из списка'}
            />
          </div>
          <InputWithLabel
            placeholder={'4508'}
            disabled={disabled}
            className={styles.containerSmall}
            value={individualPersonStore?.data?.document_series}
            onChange={(event) => {
              individualPersonStore?.setSeries(event.target.value);
            }}
            label="Серия и номер документа"
          />
          <InputWithLabel
            placeholder={'024404'}
            disabled={disabled}
            className={styles.containerSmall}
            value={individualPersonStore?.data?.document_number}
            onChange={(event) => {
              individualPersonStore?.setNumber(event.target.value);
            }}
            label=" "
          />
        </div>
        <div className={styles.container}>
          <InputWithLabel
            placeholder={'Укажите адрес'}
            disabled={disabled}
            className={styles.containerHuge}
            value={individualPersonStore?.data?.residence_address}
            onChange={(event) => {
              individualPersonStore?.setResidenceAddress(event.target.value);
            }}
            label="Адрес места жительства (временной регистрации)"
          />
        </div>

        <div
          className={styles.container}
          onClick={() => {
            individualPersonStore?.setNotCountryResidenceAddress();
          }}
        >
          <Checkbox
            onChange={() => {
              individualPersonStore?.setNotCountryResidenceAddress();
            }}
            checked={individualPersonStore?.data?.not_country_residence_address}
            Icon={commonStore?.appTheme === 'dark' ? CheckboxIconDark : CheckboxIcon}
          />
          <span className={styles.checkboxLabel}>
            Адрес места жительства не на территории Российской Федерации
          </span>
        </div>
        <div className={styles.separateLine} />
        {individualPersonStore?.errors.map((item: any, index: number) => (
          <Error
            key={'error-' + index}
            isValidFile={individualPersonStore?.isValidBasic}
            text={item}
          />
        ))}
        {!graphStore?.visibleNodes && graphStore?.ableEdit && (
          <div className={styles.actions}>
            <div
              onClick={() => {
                modalStore?.closeModal(MODAL_DETAILED_INDIVIDUAL_PERSON);
              }}
              className={styles.cancelButton}
            >
              Отменить
            </div>
            <div className={styles.action}>
              <ActionButton
                buttonStyle={
                  isFillRequired ? `${styles.button} ${styles.apply}` : `${styles.button} `
                }
                label={isShow ? 'Сохранить' : 'Добавить связь'}
                onClick={() => {
                  isShow ? this._changeNode() : this._createNewFL();
                }}
              />
            </div>
          </div>
        )}
      </div>
    );
  }
}

export default BasicInformation;
