import React, { DetailedHTMLProps, SVGAttributes } from 'react';

export const CheckboxIcon = ({
  selected,
  someSelected,
  disabled,
}: { selected?: boolean; someSelected?: boolean; disabled?: boolean } & DetailedHTMLProps<
  SVGAttributes<SVGSVGElement>,
  SVGSVGElement
>) => (
  <svg
    width="18px"
    height="18px"
    viewBox="0 0 18 18"
    fill="none"
    xmlns="http://www.w3.org/2000/svg"
  >
    <rect
      x="1"
      y="1"
      width="16"
      height="16"
      rx="2"
      stroke={disabled || (!selected && !someSelected) ? '#CCCCCC' : '#0B346A'}
    />
    <rect
      x="1"
      y="1"
      width="16"
      height="16"
      rx="2"
      fill={disabled ? '#E6E6E6' : selected || someSelected ? '#0B346A' : 'white'}
    />
    {selected && (
      <path
        transform="translate(5, 6)"
        d="M0 3.11207L3.22831 6L8 1.03736L6.79746 0L3.09553 3.8027L1.05729 1.95855L0 3.11207Z"
        fill="white"
      />
    )}
    {someSelected && <rect x="5" y="8" width="8" height="2" fill="white" />}
  </svg>
);
